// 订阅首页
<template>
    <div class="edit">
        <div v-if="formatPersonalData">
            <div class="account-bar">
                <p class="title">
                    {{$t('personal.receivingEmail')}}
                </p>
                <div class="account padding-cls flx" @click="editAccount">
                    <div class="flx-content account-user">
                        {{formatPersonalData.userId}}
                    </div>
                    <img  class="arrow" src="../../assets/arrows.png">
                </div>
            </div>
            <div class="preference-setting">
                <p class="title">
                    {{$t('personal.subscribeSetting')}}
                </p>
                <div class="preference-setting-content">
                    <div class="item-setting" @click="editDemand">
                        <h3>{{$t('personal.demandType')}}</h3>
                        <div class="flx">
                            <ul class="settings-list flx-content" v-if="formatPersonalData.requestType && formatPersonalData.requestType.length > 0">
                                <li  class="item-settins-list" v-for="(item, index) in formatPersonalData.requestType" :key="index">
                                    {{item.name}}
                                </li>
                            </ul>
                            <input type="text" v-else :placeholder="$t('personal.request')" class="keywords">
                            <img  class="arrow" src="../../assets/arrows.png">
                        </div>
                    </div>
                    <div class="item-setting" @click="editIndustry">
                        <h3>{{$t('personal.industryType')}}</h3>
                        <div class="flx">
                            <ul class="settings-list flx-content" v-if="formatPersonalData.industryTypes && formatPersonalData.industryTypes.length > 0">
                                <li class="item-settins-list" v-for="(item, index) in formatPersonalData.industryTypes" :key="index">
                                    {{item.name}}
                                </li>
                            </ul>
                            <input type="text" v-else :placeholder="$t('personal.industry')" class="keywords">
                            <img  class="arrow" src="../../assets/arrows.png">
                        </div>
                    </div>
                    <div class="item-setting" @click="editKeyword">
                        <h3>{{$t('personal.keywords')}}</h3>
                        <div class="flx">
                            <ul class="settings-list flx-content" v-if="formatPersonalData.keywords && formatPersonalData.keywords.length > 0">
                                <li class="item-settins-list" v-for="(item, index) in formatPersonalData.keywords" :key="index">
                                    {{item}}
                                </li>
                            </ul>
                            <input type="text" v-else :placeholder="$t('personal.customizeKeywords')" class="keywords">
                            <img  class="arrow" src="../../assets/arrows.png">
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <confirm-btn @confirmClick="confirmClick"></confirm-btn>
    </div>
</template>

<script>
import config from '../../config.js';
import { mapState, mapActions } from 'vuex';
import ConfirmBtn from '../../components/common/confirm-button';
import { publicPost } from '../../js/api/index.js';
import getApiUrl from '../../js/api/apiUrlList';
export default {
    data () {
        return {
            userId: this.$route.query.userId || config.userList[0],
            flg: false
        };
    },
    computed: {
        ...mapState({
            personalData: state => state.personal.personalData,
            industryTypes: state => state.personal.industryTypes,
            requestType: state => state.personal.requestType,
            keywords: state => state.personal.keywords
        }),
        formatPersonalData () {
            let personalData = JSON.parse(JSON.stringify(this.personalData));
            if (personalData) {
                // if (this.industryTypes.length > 0) {
                personalData.industryTypes = this.industryTypes;
                // }
                // if (this.requestType.length > 0) {
                personalData.requestType = this.requestType;
                // }
                // if (this.keywords.length > 0) {
                personalData.keywords = this.keywords;
                // }
                return personalData;
            }
        }
    },
    methods: {
        ...mapActions({
            getPersonalData: 'getPersonalData'
        }),
        // 编辑账号
        editAccount () {
            this.$router.push({
                path: '/edit-account',
                query: {
                    userId: this.userId
                }
            });
        },
        // 编辑需求类型
        editDemand () {
            this.$router.push({
                path: '/edit-demand',
                query: {
                    userId: this.userId
                }
            });
        },
        // 编辑行业类型
        editIndustry () {
            this.$router.push({
                path: '/edit-industry',
                query: {
                    userId: this.userId
                }
            });
        },
        // 编辑关键词
        editKeyword () {
            this.$router.push({
                path: '/edit-keyword',
                query: {
                    userId: this.userId
                }
            });
        },
        // 确认
        confirmClick () {
            let personalData = this.formatPersonalData;
            if (personalData.industryTypes) {
                personalData.industryTypes.forEach(ele => {
                    delete ele.selected;
                });
            }
            if (personalData.requestType) {
                personalData.requestType.forEach(ele => {
                    delete ele.selected;
                });
            }
            let obj = {
                userId: personalData.userId,
                industryTypeList: personalData.industryTypes,
                requestTypeList: personalData.requestType,
                keywordList: personalData.keywords

            };
            publicPost(getApiUrl('setSub'), obj).then((res) => {
                this.$router.push({
                    name: 'personal'
                });
            }).catch(() => {
                console.warn('error');
            });
        }
    },
    created () {
        this.$router.setTitle(this.$t('title.editSubscibe'));
        let obj = {
            userId: this.userId
        };
        if (!this.personalData) {
            this.getPersonalData(obj);
        }
    },
    beforeRouteEnter (to, from, next) {
        if (from.name === 'edit-account') {
            next(vm => {
                let obj = {
                    userId: to.query.userId || config.userList[0]
                };
                vm.getPersonalData(obj);
            });
        }
        next();
    },
    components: {
        ConfirmBtn
    }
};
</script>

